// custom color
$lightyellow: #FFF9E8;
// button color 
$btn-font-color: #2A2828;
// header
$header-bg: #F0F2F4;
// message
$message-bg: #F0F2F4;
// footer
$footer-bg: #EBE8E8;
$footer-light: #adadad;
$footer-font-color: #141313;
// box-shadow
$custom-box-shadow-bese: 0 3px 6px rgba($black, 0.16);
// navbar 
$nav-sub-menu-bg: #EBEAEA;
$nav-border-color: #C0C1C1;
$nav-border: 1px solid $nav-border-color;
$nav-menu-phone-bg: #ededed;
$nav-menu-phone-border-color: #AFAFAF;
$nav-menu-phone-border: 1px solid $nav-menu-phone-border-color;
$open-nav-menu-bg: #6E6567;
// login color
$login-input-bg-light: #efefef;
$login-input-shadow-inset: 2px 2px 2px rgba($black, 0.16) inset;
$login-button-bg: #DDD9D8;
$login-button-hover-bg: #707070;
$login-button-border-color: #707070;
$login-button-border: 1px solid $login-button-border-color;
$login-links-color: #0074A5;

$box-shadow: 0 3px 6px rgba($black, .16);
// icon 
$icons: (
    chevron-down: "\f078",
    angle-left: "\f104",
    angle-right: "\f105",
    plus: "\2b",
    minus: "\f068",
    right-triangle: "\e913",
    circle: "\f111",
    check: "\f00c",
    icon-arrow-right: '\e901',
    user: '\f007',
);
// 
$page-header-gradient-bg: linear-gradient(to right, #EBF3FA 0%, #DDEDFA 70%, #CFE8FA 100%);
// page-link
$pagination-custom-bg: #E0DFDF;
// shareFeedbackModal
$shareFeedbackModal-bg: #E5F0FA;
// feedback
$swiper-button-color: #DCDBDB;
// 
$custom-gray: #F4F4F4;
$custom-focus-color: #ff5900;
$breadcrumb-bg: #DBECF4;
$pagination-dark: #4A4C4D;
$custom-small-color: #919191;

$custom-accordion-border-color: #B9B2B2;
$custom-characterTab-bg: #f2f2f2;
// search
$custom-search-bg: #ECEFEF;
$custom-search-border: #C4C4C4;
$custom-search-link-colr: #0581B4;
$custom-light-blue: #48B6E5;
$custom-light-orange: #F8C886;
$custom-light-purple: #E1D0FC;
$custom-light-green: #CBEDBC;
$custom-search-tag-color: (
    lightBlueIcon: $custom-light-blue,
    lightOrangeIcon: $custom-light-orange,
    lightPurpleIcon: $custom-light-purple,
    lightGreenIcon: $custom-light-green
) !default;
$custom-tag-light-gray: #F5F4F4;
$professionalFunctions-navbar-bg: #F8F8F8;
// model
$custom-model-bg-color: #D1D1D1;
// 
$custom-dark-blue: #006793;
$custom-accordion-header-color: $custom-dark-blue;
$custom-accordion-header-hover-color: #E6F2F7;

